import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    memberinfo: [],
    eventid: "",
  },
  mutations: {
    setmemberinfolength(state, payload) {
      state.memberinfo.length = payload;
    },
    setmemberinfo(state, payload) {
      state.memberinfo[payload.index] = payload.value;
    },
    seteventid(state, payload) {
      state.eventid = payload;
    },
  },
  actions: {
    // getSignupinfo() {
    //   axios({
    //     method: "get",
    //     url:
    //       "https://script.google.com/macros/s/AKfycbwu0IumvrBT9W5XybuCu-_hQxn8r8i37EQ-ziiR0N3Mfjkilx2ww_NRS8kEtqNMKV_I/exec",
    //     params: {
    //       mode: "query",
    //       id: "950ac145-5aa9-4456-b48a-03e0b2da05ff",
    //     },
    //   }).then((response) => {
    //     this.eventinfo = response.data[0];

    //     this.regisinfo = response.data[0][9].split("、");
    //     this.$store.commit("setmemberinfolength", this.regisinfo.length);
    //     console.log(response.data[0]);
    //   });
    // },
    postmemberinfo(context) {
      let postmemberinfo = "";
      for (let i = 0; i < context.state.memberinfo.length; i++) {
        if (
          context.state.memberinfo[i] == "" ||
          context.state.memberinfo[i] === undefined
        ) {
          console.log("有歐");
          postmemberinfo = postmemberinfo + "空" + "、";
        } else {
          postmemberinfo = postmemberinfo + context.state.memberinfo[i] + "、";
        }
      }
      postmemberinfo = postmemberinfo.substring(0, postmemberinfo.length - 1);

      let postdata = {
        data: postmemberinfo,
        id: context.state.eventid,
      };
      // console.log(postdata);

      axios
        .get(
          "https://script.google.com/macros/s/AKfycbx7xjU8x30mpb2Ms5skkCF4ihdTKzGIX3MoR9YrgivabfPmFe-e_v3TsjFQGOsqkUCWWQ/exec",
          {
            params: {
              memberinfo: postdata.data,
              id: postdata.id,
            },
          }
        )
        .then((res) => {
          console.table(res.data);
        })
        .catch((error) => {
          console.error(error);
        });
    },
  },
  modules: {},
});
